import React, { Component } from 'react'
import './Data.css';

export default class Data extends Component {

    constructor(props) {
        super(props)
    }

    componentDidMount() {
        setTimeout(function() {this.props.history.push("/data"); }.bind(this), 1000);
    }


    componentWillUnmount() {

    }

    componentWillReceiveProps(newProps) {
    }

    createProperties() {

    }
    loadProperties() {

    }
    render() {
        return (
        <div className="">
            <div className="">
                <div className="row row-p">
                    <div className="div-info">
                        <p className="make-spacing">Retrouvez ici les données associées aux comptes que vous avez connecté</p>
                        <img  src="asset/img/btnadd.png" />
                        <div className="hr make-margin" ></div>
                        <p className="make-spacing">Vous pouvez naviguer dans vos données grâce aux filtres temporels ci-dessus </p>
                    </div>
                </div>
                <div className="row row-p a-data" >
                    <div className="col-5">
                        <h4 className="title-bloc">
                            Répartition du temps par usage
                        </h4>
                        <select name="usage" id="usage" className="soption-data">
                            <option value="hebdomadaire">Hebdomadaire</option>
                            <option value="mensuel">Mensuel</option>
                            <option value="annuel">Annuel</option>
                        </select>
                        <div className="row row-c">
                            <div className="col-3 border-green">
                                <p>téléphone</p>
                                <p>web</p>
                                <p>video</p>
                            </div>
                            <div className="col-3 rm-p">
                                <div className="baton"></div>
                                <div className="baton"></div>
                                <div className="baton"></div>
                            </div>
                        </div>
                    </div>
                    <div className="col-5 hr-verti">
                        <h4 className="title-bloc" >
                            Répartition du temps par support
                        </h4>
                        <select name="support" id="support" className="soption-data">
                            <option value="hebdomadaire">Hebdomadaire</option>
                            <option value="mensuel">Mensuel</option>
                            <option value="annuel">Annuel</option>
                        </select>
                        <div className="diagramme"></div>
                    </div>
                </div>
            </div>
            <div className="row div-legend">
                <div className="data-legend">
                    <div className="l-fb"></div>
                    <p>FACEBOOK</p>
                </div>
                <div className="data-legend">
                    <div className="l-or"></div>
                    <p>ORANGE</p>
                </div>
            </div>
        </div>
                
        )
    }
}




