import {DOCTYPE_PROPERTIES} from "../constants/index";
import {getDataElements} from "../tools/index";

export const PROPERTIES_LOAD = "PROPERTIES_LOAD"
export const PROPERTIES_LOAD_SUCCESS = "PROPERTIES_LOAD_SUCCESS"
export const PROPERTIES_LOAD_FAILURE = "PROPERTIES_LOAD_FAILURE"

export const PROPERTIES_CREATE = "PROPERTIES_CREATE"
export const PROPERTIES_CREATE_SUCCESS = "PROPERTIES_CREATE_SUCCESS"
export const PROPERTIES_CREATE_FAILURE = "PROPERTIES_CREATE_FAILURE"

export const PROPERTIES_REMOVE = "PROPERTIES_REMOVE"

export const propertiesLoad = () => {
    return async(dispatch, getState) => {
        dispatch({type: PROPERTIES_LOAD})
        try {
            const data =  await cozy.client.data.findAll(DOCTYPE_PROPERTIES)
            return dispatch({type: PROPERTIES_LOAD_SUCCESS, data, DOCTYPE_PROPERTIES})
        } catch (err) {
            return dispatch({type: PROPERTIES_LOAD_FAILURE, err})
        }
    }
}

export const propertiesCreate = (doctype, value) => {
    return async(dispatch, getState) => {
        let data = {doctype,value};
        dispatch({type: PROPERTIES_CREATE, data, DOCTYPE_PROPERTIES})

        try {
            let res = await getDataElements(DOCTYPE_PROPERTIES)
            for(const e of res){
                console.log(e)
                console.log(doctype)
                if(e.doctype == doctype){
                    await cozy.client.data.delete(DOCTYPE_PROPERTIES, e)
                    dispatch({type: PROPERTIES_REMOVE, data, DOCTYPE_PROPERTIES})
                }

            }

            const newData = await cozy.client.data.create(DOCTYPE_PROPERTIES, data)
            return dispatch({type: PROPERTIES_CREATE_SUCCESS, data: newData, DOCTYPE_PROPERTIES})
        } catch (err) {
            return dispatch({type: PROPERTIES_CREATE_FAILURE, err})
        }
    }
}




