import { connect } from 'react-redux'
import { propertiesLoad, propertiesCreate } from '../actions'
import Dashboard from '../components/elements/Dashboard'
import {dataLoad, dataLoadAll} from "../actions/data";

const mapDispatchToProps = dispatch => {
    return {
        onLoadProperties: () => {
            dispatch(propertiesLoad())
        },
        onCreateProperties: (doctype, value) => {
            dispatch(propertiesCreate(doctype, value))
        },
        onLoadData: (doctype, id) => {
            dispatch(dataLoad(doctype, id))
        },
        onLoadDataAll: (doctype) => {
            dispatch(dataLoadAll(doctype))
        }
    }
}

const ConnectedDashboard = connect(
    null,
    mapDispatchToProps
)(Dashboard)

export default ConnectedDashboard


