import React, { Component } from 'react'
import { withRouter, Link } from 'react-router-dom'
import './Sidebar.css';

export default class Sidebar extends Component {

    constructor(props) {
        super(props)
    }

    componentDidMount() {

    }


    componentWillUnmount() {

    }

    componentWillReceiveProps(newProps) {
    }

    createProperties() {

    }
    loadProperties() {

    }
    render() {
        return (
           <nav>
               <ul className={"Sidebar-list"}>
                   <li className={"Sidebar-link"}>
                       <ActiveLink to="/home" activeClassName={"Sidebar-link-active"} title='Accueil'>
                           Accueil
                       </ActiveLink>
                   </li>
                   <li className={"Sidebar-link"}>
                       <ActiveLink to="/data" activeClassName={"Sidebar-link-active"} title='Mes Données'>
                           Mes Données
                       </ActiveLink>
                   </li>
                   <li className={"Sidebar-link"}>
                       <ActiveLink to="/about" activeClassName={"Sidebar-link-active"} title='À propos'>
                           À propos
                       </ActiveLink>
                   </li>
                   <li className={"Sidebar-link"}>
                       <ActiveLink to="/contact" activeClassName={"Sidebar-link-active"} title='Contact'>
                           Contact
                       </ActiveLink>
                   </li>
               </ul>
           </nav>
        )
    }
}


export const CustomLink = (props) => {
    const open = (e) => {
        e.preventDefault()
        if(props.onClick) props.onClick()
        props.history.push(props.to)
    }

    const { router, location, to, activeClassName, children, title } = props

    const active = location.pathname.indexOf(to) === 0

    return (
        <a className={active && activeClassName} onClick={e => open(e)} title={title}>
            {children}
        </a>
    )
}

const ActiveLink = withRouter(CustomLink)


