

export const getDataElements = async(doctype, addDoctypeInElements = false) => {
    const indexRef = await cozy
        .client
        .data
        .defineIndex(doctype, ['_id'])
    return await cozy
        .client
        .data
        .query(indexRef, {
            selector: {
                "_id": {
                    "$gt": null
                }
            }
        })
        .then(list => {
            if(addDoctypeInElements) return list.map(e => ({...e, doctype}))
            return list
        })
}
export const getData = async(doctype, addDoctypeInElements = false) => {
    const indexRef = await cozy
        .client
        .data
        .defineIndex(doctype, ['_id'])

    return await cozy
        .client
        .data
        .query(indexRef, {
            selector: {
                "_id": {
                    "$gt": null
                }
            }
        })
        .then(list => {
            if(addDoctypeInElements) return list.map(e => ({...e, doctype}))
            return list
        })
}


