import React, { Component } from 'react'
import Page from "./Page";
import {DATA_LIST, DOCTYPE_PROPERTIES, DOCTYPE_ORANGE_PHONECOMMUNICATIONLOG} from "../../constants/index"
import {getDataElements} from "../../tools/index";
import _ from 'lodash';

import './Dashboard.css';
import {getData} from "../../tools";

export default class Dashboard extends Component {

    constructor(props) {
        super(props)

        this.state = {
            konnector: [
                {
                    doctype: DOCTYPE_ORANGE_PHONECOMMUNICATIONLOG,
                    value: 'false'
                }
            ]
        }


    }

    componentDidMount() {
        setTimeout(function() {
            this.loadProperties()
        }.bind(this), 1000);
    }


    componentWillUnmount() {

    }

    componentWillReceiveProps(newProps) {
    }

    createProperties(doctype, value) {
       this.props.onCreateProperties(doctype, value)
        setTimeout(function() {
            this.loadProperties()
        }.bind(this), 1000);
    }
    loadProperties() {
        return getDataElements(DOCTYPE_PROPERTIES)
            .then((result) => {
                let konnector = [];
                DATA_LIST.map((item) => {
                    let exist = _.find(result, {doctype: item.doctype})
                    if(exist){

                        konnector.push(exist);

                        getData(item.doctype)
                            .then((result) => {
                                console.log(result)
                            })
                    }else{
                        konnector.push({
                            doctype: item.doctype,
                            value: false
                        })
                    }
                });
                this.setState({konnector})
            })
    }

    render() {
        return (
            <Page title="">
                <div className="row Dashboard-title">
                    <div className="offset-md-2 col-2">
                        <img src="asset/img/visuel_service.png" class="img-fluid"  alt=""/>
                    </div>
                    <div className="col-4">
                        Le service Optime rend visible le temps que vous passez sur vos écrans. Les données sur lesquelles nous nous appuyons sont les données d'utilisation Orange ainsi que vos actions sur Facebook.                     </div>
                </div>
                <div className={"row"}>
                    <div className="offset-md-2">
                        <div className="row">
                            {
                                DATA_LIST.map((item) => {
                                    return(
                                        <div className={"col-5 Dashboard-card"}>
                                            <h1>
                                                {item.name}
                                            </h1>
                                            <h2>
                                                {item.excerpt}
                                            </h2>
                                            {
                                                item.usages.map((usage) =>{
                                                    return(
                                                        <div className={'usages-item'}>
                                                            <img src={usage.url} alt=""/>
                                                            <p className="usages-excerpt">
                                                                {usage.text}
                                                            </p>
                                                        </div>
                                                    )
                                                })
                                            }
                                            <div className={"Dashboard-card-actions"}>
                                                {
                                                    this.state.konnector.map((konnector) => {
                                                        if(item.doctype == konnector.doctype){
                                                            if(konnector.value){
                                                                return(
                                                                    <button  className={"button_logout"} onClick={() => this.createProperties(item.doctype, false)}>

                                                                    </button>
                                                                )
                                                            }else{
                                                                return(
                                                                    <button className={"button_login"} onClick={() => this.createProperties(item.doctype, true)}>

                                                                    </button>
                                                                )
                                                            }
                                                        }
                                                    })}
                                            </div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </Page>
        )
    }
}


